@use 'variables' as *;

html {
    font-size: 100%; /*16px*/
}

h1,
h2,
h3,
h4,
h5 {
    margin: 0;
    line-height: 1.15;

    + p {
        margin-top: 1em;
    }
}

h1 {
    font-size: 3.052em;
}

h2 {
    font-size: 2.441em;
}

h3 {
    font-size: 1.953em;
}

h4 {
    font-size: 1.563em;
}

h5 {
    font-size: 1.25em;
}

small,
.text_small {
    font-size: 0.8em;
}

p {
    margin: 0;

    + p {
        margin-top: 1em;
    }
}

// font families
.font-sans {
    font-family: $font-sans;
}

.font-serif {
    font-family: Georgia, Cambria, 'Times New Roman', Times, serif;
}

.font-mono {
    font-family: $font-mono;
}

// text-transform
$textTransforms: (uppercase, lowercase, capitalize);

@each $transform in $textTransforms {
    .#{$transform} {
        text-transform: #{$transform};
    }
}
